<template><section><h1>ClassificationPrimitive</h1>
<p>The <code v-pre>vc-primitive-classification</code> component is used to load a classification primitive represents a volume enclosing geometry in the Scene to be highlighted. Valid geometries are BoxGeometry, CylinderGeometry, EllipsoidGeometry, PolylineVolumeGeometry, and SphereGeometry. Geometries that follow the surface of the ellipsoid, such as CircleGeometry, CorridorGeometry, EllipseGeometry, PolygonGeometry, and RectangleGeometry, are also valid if they are extruded volumes; otherwise, they will not be rendered.</p>
<h2>Example</h2>
<h3>Load ClassificationPrimitive with <code v-pre>vc-primitive-classification</code></h3>
<h4>Preview</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready">
        <vc-provider-terrain-cesium></vc-provider-terrain-cesium>
        <vc-primitive-classification :asynchronous="false">
          <vc-instance-geometry :geometry.sync="geometry" :attributes="attributes">
            <vc-geometry-polygon :polygonHierarchy="polygonHierarchy" :extrudedHeight="extrudedHeight"></vc-geometry-polygon>
          </vc-instance-geometry>
        </vc-primitive-classification>
      </vc-viewer>
    </div>
  </template>
  
</doc-preview>
